@import "styles/variables.scss";

.chat {
    position: fixed;
    right: 24px;
    bottom: 24px;
    z-index: 100000;
    //transition: 0.150s;
    opacity: 0;
}

.isNotOpacity {
    opacity: 1;
}

.info,
.defaultBtn {
    padding: 14px 20px;
    border: 1px solid $darkBlue;
    background-color: $beige;
}

.defaultBtn {
    cursor: pointer;
}

.info {
    display: flex;
    position: relative;
}

.closeBtn {
    position: absolute;
    top: 21px;
    left: 21px;
    background-color: $beige;
    cursor: pointer;
}

.data {
    margin-top: 50px;
}

.data > p {
    margin-bottom: 40px;
    margin-right: 32px;
}

.contacts > p {
    margin-bottom: 8px;
}

.contacts > a {
    text-decoration: none;
}

.mobileBtn {
    display: none;
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
}

.mobileBtn > button {
    width: 95vw;
    border: 1px solid $darkBlue;
    background-color: $beige;
    font-size: 16px;
    padding: 14px 0;
    text-transform: uppercase;
}

@media screen and (min-width: 1681px) {
    .info {
        width: max-content;
        transform: translateX(-320px);
    }
}

@media #{$tablet} {
    .defaultBtn {
        font-size: 16px;
    }
    .info {
        width: calc(100vw + 2px);
        height: 130px;
        padding: 0;
        transform: translateY(1px) translateX(-1px);
        box-sizing: border-box;
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
    }
    .contacts {
        display: none;
    }
    .info > :last-child {
        visibility: hidden;
    }
    .closeBtn {
        top: 13px;
        right: 13px;
        left: auto;
    }
    .data {
        margin: 12px;
    }
    .data > p {
        font-size: 16px;
        min-width: 190px;
    }
    .mobileBtn {
        display: block;
    }
}